.blogpostContainer {
  width: 400px;
  max-width: 100%;
  background-color: #adcbe3;
  padding-bottom: 15px;
}

.blogpostContainer h1 {
  color: #4b86b4;
  font-size: 2em;
  margin-left: 20px;
}

.blogpostContainer h3 {
  color: #2a4d69;
  font-size: 0.9em;
  font-family: 'Montserrat';
  margin-left: 20px;
  margin-top: -30px;
}

.blogpostContainer p, .blogpostContainer hr {
  max-width: 100%;
  width: 360px;
  display: block;
  margin: 0 auto;
  text-align: left;
}

.blogpostContainer hr {
  border: 2px solid #e7eff6;
  margin-top: 3px;
  margin-bottom: 10px;
}

.blogpostContainer .author {
  margin-left: 35px;
  margin-top: 10px;
  font-size: 0.9em;
  padding-left: 5px;
  border-left: 2px solid #4b86b4;
}

.blogpostContainer form {
  margin-left: 20px;
  margin-top: 15px;
}

.blogpostContainer textarea {
  font-family: 'Montserrat';
  width: calc(100% - 40px);
  resize: none;
  border-radius: 10px;
  padding-top: 5px;
}

.blogpostContainer .lg-btn {
  margin-top: 10px;
}

.blogpostContainer input::-webkit-input-placeholder {
  font-family: 'Montserrat';
}

.blogpostContainer input::-moz-placeholder {
  font-family: 'Montserrat';
}

.blogpostContainer input:-ms-input-placeholder {
  font-family: 'Montserrat';
}

.blogpostContainer input::placeholder {
  font-family: 'Montserrat';
}

.navContainer {
  width: 400px;
  max-width: 100%;
}

.navContainer nav a {
  width: 20px;
}

.navContainer nav a img {
  width: 100%;
}

.navContainer nav ul {
  display: block;
  background-color: #adcbe3;
  padding: 0;
  list-style-type: none;
}

.navContainer nav ul hr {
  border: 2px solid #e7eff6;
}

.navContainer nav ul li {
  max-width: 100%;
  text-align: left;
  padding-left: 30px;
}

.navContainer nav ul li a {
